  visibility: hidden;
  padding-right: 10px;
}

.select {
  cursor: pointer;
  display: inline-block;
  position: relative;
  color: #fff;
  width: 35px;
  height: 30px;
  font-size: 16px;
  color: #fff;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin-right: 30px;
}

.select-styled {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  left: 0;
  -webkit-border-radius: 0;
  border-radius: 0;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  color: #fff;
  font-size: 16px;
  text-transform: uppercase;
  z-index: 1;
}



.select-styled:focus {
  outline: 0;
  text-decoration: none;
}

.select-styled.active,
.select-styled:active {
  color: rgba(255, 255, 255, 0.5);
}

.select-styled.active:after,
.select-styled:active:after {
  -webkit-transform: rotateX(-180deg);
  transform: rotateX(-180deg);
  opacity: 0.5;
}

.select-styled:hover {
  color: rgba(255, 255, 255, 0.5);
}

.select-styled:hover:after {
  opacity: 0.5;
}

.select-options {
  display: none;
  width: 80px;
  background-color: #fff;
  position: absolute;
  top: 45px;
  right: 0;
  z-index: 2;
  margin: 0;
  padding: 0;
  list-style: none;
  padding: 24px 28px 22px 29px;
}

.select-options:before {
  content: "";
  position: absolute;
  top: -10px;
  right: 0;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 0 solid transparent;
  border-bottom: 10px solid #fff;
}

.select-options li {
  color: #fff;
  margin: 0;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  font-size: 16px;
  text-transform: uppercase;
  color: #777;
  margin-bottom: 15px;
}

.select-options li:last-child {
  margin-bottom: 0;
}

.select-options li:hover {
  opacity: 0.5;
}

.select-options li[rel="hide"] {
  display: none;
}

