.doc {
  height: 275px;
  width: 100%;
  display: block;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  -webkit-transition: background-color 0.4s, -webkit-transform 0.6s,
    -webkit-box-shadow 0.4s;
  transition: background-color 0.4s, -webkit-transform 0.6s,
    -webkit-box-shadow 0.4s;
  -o-transition: background-color 0.4s, transform 0.6s, box-shadow 0.4s;
  transition: background-color 0.4s, transform 0.6s, box-shadow 0.4s;
  transition: background-color 0.4s, transform 0.6s, box-shadow 0.4s,
    -webkit-transform 0.6s, -webkit-box-shadow 0.4s;
  position: relative;
  margin-bottom: 10px;
}

.doc:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  background-color: #fff;
