  .home-body:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 110%;
      background-image: url(https://media.geeksforgeeks.org/wp-content/uploads/20200828184719/rain-300x300.png);
      animation: rain 1.4s linear infinite;
      opacity: 0;
  }



  @font-face {
      font-family: custom;
      src: url('../assets/RNS_Camelia.otf');
  }

  .home-body {
      width: 100%;
      height: 100%;
      display: flex;
      width: 100%;
      flex-direction: column;
      display: flex;
      width: 100%;
      flex-direction: column;
      align-items: center;

      .full-img {
          width: 100%;
          height: 90vh;
          background-position: center;
      }

      .cIjgB {
          width: 80%;


      }

      .bYgUJS {
          width: 100%;
          position: relative;
          display: flex;
          flex-direction: column;
          -webkit-box-align: center;
          align-items: center;
          -webkit-box-pack: center;
          justify-content: center;
          margin-bottom: 100px;
      }

      .chart {


          .fDDjHD {
              width: 40%;
          }
      }

      @keyframes rain {
          0% {
              background-position: 0 0;
              opacity: 1;
          }

          100% {
              background-position: 8% 80%;
              opacity: 1;
          }
      }





      background-color: #080808;

      .background-video {
          position: absolute;
          top: 0px;
          left: 0px;
          width: 100%;
          height: 100%;
          object-fit: cover;
          opacity: 0.25;
          z-index: -5;
      }

      .bottom-svg {
          width: 100%;
      }

      .road__item-content {
          width: 150%;
          text-align: left;

          p {
              font-family: custom;
              font-size: 25px;

          }
      }

      .roadmap-section {
          display: flex;
          flex-direction: row;
          width: 80%;
          position: relative;



          .left {


              width: 50%
          }
      }

      .seperator-tokenomics {
          width: 100%;
          display: flex;
          align-items: center;
          flex-direction: row;
          justify-content: center;

          img {
              height: auto;
              max-width: 100%;
              vertical-align: middle;
          }

          .fuJtcf {
              width: 25%;
          }

          .jREztg {
              width: 20%;
          }

          .jcqGXt {
              width: 420px;
              position: absolute;
              right: 0px;
              top: 0px;
          }
      }



      .section-sass {
          margin-top: 20px;
          margin-bottom: 20px;
          display: flex;
          width: 100%;
          align-items: center;
          justify-content: center;
          flex-direction: column;

          .btn-row {
              width: 70%;

              flex-direction: row;
              display: flex;
              align-items: center;
              justify-content: space-around;

              .customizedButtons {
                  width: 18rem;
                  height: 4rem;
                  border-radius: 50px;
                  border: none;
                  /*   background-color: #92C03E; */
                  color: white;
                  font-weight: bold;
                  font-size: large;
                  border-color: #92C03E;
                  box-shadow: #92C03E 0px 0px 14px, #92C03E 0px 0px 15px, #92C03E 0px 0px 26px;
                  background: rgb(18, 21, 27);
                  border-width: 3px;
                  border-style: solid;

              }

              .customizedButtons:hover {
                  background: rgb(66, 74, 87);
              }
          }



          p {
              font-weight: bold;
              color: white;
              font-size: xx-large;
              width: 80%;
          }

          .main-image {
              background-image: url(../assets/main.png);
              height: 60vh;
              width: 80%;
              background-repeat: no-repeat;
              background-size: contain;
              margin-top: 150px;
              margin-bottom: 20px;
              background-position: center center;
          }
      }

      .top-nav {
          height: auto;
          width: 100%;
          display: flex;
          flex-direction: column;
          background: transparent;
          padding-top: 20px;
          z-index: 5;
          padding-bottom: 20px;
          -webkit-box-align: center;
          align-items: center;
          flex-direction: row;
          -webkit-box-pack: justify;
          justify-content: space-between;
          position: fixed;
          background-color: transparent;

          .middle {
              display: flex;
              align-items: center;
              margin-top: 10px;

              p {
                  color: white;
                  margin-left: 50px;
                  font-size: x-large;
                  font-weight: bold;
              }

              p:hover {
                  color: #92C03E;
              }
          }

          .right {
              background-color: #e12c2c;
              margin-right: 80px;
              height: auto;
              width: 10rem;
              padding: 10px;
              color: white;
              font-weight: bold;
              border: none;
              border-radius: 25px;

          }

          .logo {
              width: 80px;
              height: 80px;
              margin-left: 40px;
              margin-top: -5px;
              background-size: contain;
              background-repeat: no-repeat;
              background-image: url(../assets/minimal.png);

          }

      }

  }